import { login, register } from "@/api/user";
// 仅在客户端加载 js-cookie 包
const Cookie = process.client ? require("js-cookie") : undefined;
export default {
  middleware: "not-auth",
  name: "LoginIndex",
  components: {},
  props: {},
  data() {
    return {
      user: {
        username: "sds",
        email: "734692305@qq.com",
        password: "liuhuijun1"
      },
      errors: {} // 错误信息
    };
  },
  computed: {
    isLogin() {
      return this.$route.name === "login";
    }
  },
  watch: {},
  created() {},
  mounted() {},
  methods: {
    async onSubmit() {
      try {
        // 提交表单请求登录
        const { data } = this.isLogin
          ? await login({
              user: this.user
            })
          : await register({
              user: this.user
            });
        console.log(data);
        // TODO: 保存用户的登录状态
        this.$store.commit("setUser", data.user);

        // 为了防止刷新页面数据丢失，我们需要把数据持久化
        Cookie.set("user", data.user);
        // 跳转到首页
        this.$router.push("/");
      } catch (err) {
        // console.dir(error);
        this.errors = err.response.data.errors;
      }
    }
  }
};
